import { makeAutoObservable } from "mobx";

//     GET -    
//     CHANGE -    
class HeightBlockStore {
  CurHeight: any = 0;

  constructor() {
    makeAutoObservable(this);
  }

  get getCurHeight() {
    return this.CurHeight;
  }

  ChangeCurHeight = (value: string) => {
    this.CurHeight = value;
  };
}

export { HeightBlockStore };
